body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #000000;
  color: #ffffff;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  box-sizing: border-box;
}

.app {
  height: 100vh;
  display: flex;
  flex-direction: column;
}

.header {
  background-color: #000000;
  padding: 1rem;
  border-bottom: 1px solid #333;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-shrink: 0;
}

.header h1 {
  margin: 0;
  color: #ff6b35;
  font-size: 1.5rem;
}

.stats {
  display: flex;
  gap: 2rem;
  font-size: 0.9rem;
  color: #ffffff;
}

.stat {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.stat-value {
  font-weight: bold;
  color: #ff6b35;
  font-size: 1.1rem;
}

.map-container {
  flex: 1;
  position: relative;
  background-color: #1a1a1a;
}

/* Ensure map tiles have dark background during loading */
.leaflet-container {
  background-color: #1a1a1a !important;
}

.leaflet-tile-container {
  background-color: #1a1a1a !important;
}

.leaflet-tile {
  background-color: #1a1a1a !important;
}

/* Dark theme for Leaflet popups */
.leaflet-popup-content-wrapper {
  background-color: #1a1a1a !important;
  color: #ffffff !important;
  border: 1px solid #444 !important;
  border-radius: 8px !important;
}

.leaflet-popup-tip {
  background-color: #1a1a1a !important;
  border: 1px solid #444 !important;
}

.leaflet-popup-close-button {
  color: #ffffff !important;
  background-color: transparent !important;
}

.leaflet-popup-close-button:hover {
  color: #ff6b35 !important;
}

.controls {
  position: absolute;
  top: 1rem;
  right: 1rem;
  z-index: 1000;
  background-color: rgba(0, 0, 0, 0.9);
  padding: 1rem;
  border-radius: 8px;
  border: 1px solid #222;
  min-width: 200px;
}

.control-group {
  margin-bottom: 1rem;
}

.control-group:last-child {
  margin-bottom: 0;
}

.control-group label {
  display: block;
  margin-bottom: 0.5rem;
  font-size: 0.9rem;
  color: #ffffff;
}

.control-group input,
.control-group select {
  width: 100%;
  padding: 0.5rem;
  border: 1px solid #444;
  border-radius: 4px;
  background-color: #1a1a1a;
  color: #fff;
  font-size: 0.9rem;
}

.control-group input:focus,
.control-group select:focus {
  outline: none;
  border-color: #ff6b35;
}

/* Range input styling */
input[type="range"] {
  -webkit-appearance: none;
  appearance: none;
  background: transparent;
  cursor: pointer;
  height: 6px;
  border-radius: 3px;
  background: #333;
}

input[type="range"]::-webkit-slider-thumb {
  -webkit-appearance: none;
  appearance: none;
  background: #9b59b6;
  height: 18px;
  width: 18px;
  border-radius: 50%;
  cursor: pointer;
  border: 2px solid #8e44ad;
}

input[type="range"]::-moz-range-thumb {
  background: #9b59b6;
  height: 18px;
  width: 18px;
  border-radius: 50%;
  cursor: pointer;
  border: 2px solid #8e44ad;
  border: none;
}

input[type="range"]::-webkit-slider-track {
  background: #333;
  height: 6px;
  border-radius: 3px;
}

input[type="range"]::-moz-range-track {
  background: #333;
  height: 6px;
  border-radius: 3px;
  border: none;
}

.button {
  background-color: #ff6b35;
  color: white;
  border: none;
  padding: 0.5rem 1rem;
  border-radius: 4px;
  cursor: pointer;
  font-size: 0.9rem;
  transition: background-color 0.2s;
}

.button:hover {
  background-color: #e55a2b;
}

.button:disabled {
  background-color: #444;
  cursor: not-allowed;
}

.loading {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 1000;
  background-color: rgba(0, 0, 0, 0.9);
  padding: 2rem;
  border-radius: 8px;
  border: 1px solid #222;
  text-align: center;
}

.loading-spinner {
  border: 3px solid #222;
  border-top: 3px solid #ff6b35;
  border-radius: 50%;
  width: 30px;
  height: 30px;
  animation: spin 1s linear infinite;
  margin: 0 auto 1rem;
}

.progress-bar {
  width: 100%;
  max-width: 400px;
  margin: 1rem auto;
  background-color: #222;
  border-radius: 10px;
  overflow: hidden;
  position: relative;
  height: 30px;
}

.progress-fill {
  height: 30px;
  background: linear-gradient(90deg, #9b59b6, #8e44ad);
  transition: width 0.3s ease;
  border-radius: 10px;
}

.progress-text {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: white;
  font-size: 0.8rem;
  font-weight: bold;
  text-shadow: 1px 1px 2px rgba(0,0,0,0.7);
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

.user-popup {
  max-width: 200px;
  min-width: 150px;
  background-color: #1a1a1a;
  color: #ffffff;
  border: 1px solid #444;
  border-radius: 8px;
  padding: 10px;
  word-wrap: break-word;
  overflow-wrap: break-word;
  hyphens: auto;
  z-index: 1200;
}

.user-popup img {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  object-fit: cover;
  margin-bottom: 0.5rem;
}

.user-popup h3 {
  margin: 0 0 0.5rem 0;
  font-size: 1rem;
  color: #ff6b35;
}

.user-popup p {
  margin: 0 0 0.5rem 0;
  font-size: 0.8rem;
  color: #ffffff;
  word-wrap: break-word;
  overflow-wrap: break-word;
  hyphens: auto;
}

.user-popup .location {
  font-size: 0.8rem;
  color: #cccccc;
  font-style: italic;
  word-wrap: break-word;
  overflow-wrap: break-word;
}

.cluster-popup {
  text-align: center;
  background-color: #1a1a1a;
  color: #ffffff;
  border: 1px solid #444;
  border-radius: 8px;
  padding: 10px;
  z-index: 1200;
}

.cluster-popup h3 {
  margin: 0 0 0.5rem 0;
  color: #ff6b35;
}

.cluster-popup p {
  margin: 0;
  color: #ffffff;
  font-size: 0.9rem;
}

.error {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 1000;
  background-color: rgba(220, 53, 69, 0.9);
  color: white;
  padding: 2rem;
  border-radius: 8px;
  text-align: center;
  max-width: 400px;
}

.error h2 {
  margin: 0 0 1rem 0;
}

.error p {
  margin: 0;
  font-size: 0.9rem;
}

.loading-overlay {
  position: absolute;
  top: 20px;
  left: 20px;
  background: rgba(0, 0, 0, 0.8);
  color: white;
  padding: 15px;
  border-radius: 10px;
  z-index: 500;
  min-width: 300px;
  backdrop-filter: blur(5px);
}

.loading-content {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 10px;
}

.loading-content p {
  margin: 0;
  font-size: 0.9rem;
  text-align: center;
}
